@charset "UTF-8";
*{margin:0;padding:0}
.mac,.win{width:500px;height:100px;overflow-y:auto}
.win{border:1px solid #000}
.mac{border:1px solid red}
.mac::-webkit-scrollbar{-webkit-appearance:none;width:17px;background:#f1f1f1}
.mac::-webkit-scrollbar-thumb{margin:0 1px;border-left:2px solid #f1f1f1;border-right:2px solid #f1f1f1;background-color:#c1c1c1}
.mac::-webkit-scrollbar-thumb:hover{background-color:#A9A9A9}
.mac::-webkit-scrollbar-button:vertical:increment{background:url(../images/vertical-increment-arrow.png) center no-repeat}
.mac::-webkit-scrollbar-button:vertical:increment:hover{background-color:#D2D2D2}
.mac::-webkit-scrollbar-button:vertical:decrement{background:url(../images/vertical-decrement-arrow.png) center no-repeat}
.mac::-webkit-scrollbar-button:vertical:decrement:hover{background-color:#D2D2D2}